<template>
  <div class="TreatmentPrint">
    <div>
      <v-row>
        <v-col cols="3"
          ><h3>{{ cData.company_name }}</h3></v-col
        >
        <v-col cols="6">
          <h1 class="text-center">
            <strong>個案服務處遇計畫表</strong>
          </h1>
        </v-col>
        <v-col cols="3"></v-col>
      </v-row>
    </div>

    <div>
      <table
        class="form_title text-nowrap"
        style="border: 2px #000000 solid"
        width="100%"
      >
        <tr>
          <td class="font-weight-bold">一、住民基本資料</td>
        </tr>

        <tr>
          <td>
            <v-row>
              <v-col cols="2">姓名: {{ uData.name }}</v-col>
              <v-col cols="2">性別: {{ uData.sex }}</v-col>
              <v-col cols="2">生日: {{ uData.birthday }}</v-col>
              <v-col cols="3">入住日期: {{ uData.in_date }}</v-col>
              <v-col cols="3">評估日期: {{ list.date }}</v-col>
            </v-row>
          </td>
        </tr>

        <tr>
          <td>
            <v-row>
              <v-col cols="3">身分別: {{ filteridentity }}</v-col>
              <v-col cols="3">障礙類別: {{ uData.obstacle.length ? uData.obstacle[0].kind : "" }}</v-col>
              <v-col cols="3">障礙等級: {{ uData.obstacle.length ? uData.obstacle[0].lv : "" }}</v-col>
              <v-col cols="3">教育程度: {{ filtereducation }}</v-col>
              <v-col cols="12">致障原因: {{ list.obstaclereason }}</v-col>
            </v-row>
          </td>
        </tr>

        <!-- <tr>
          <td class="font-weight-bold">二、家系圖</td>
        </tr>
        <tr>
          <td class="font-weight-bold">
            <v-img
              :src="list.pic_url"
              :lazy-src="
                this.$store.state.base_url + 'upload/familytree/lazypic.jpg'
              "
              class="grey lighten-2"
              height="500px"
              contain
            ></v-img>
          </td>
        </tr> -->

        <tr>
          <td class="font-weight-bold">二、社會心理功能診斷分析</td>
        </tr>

        <tr>
          <td>
            <v-row>
              <v-col cols="12"
                >1.生活自理能力: {{ list.selfcareability }}</v-col
              >
              <v-col cols="12"
                >2.生活適應能力: 適應評估表: {{ filteradaptability }} 分</v-col
              >
              <v-col cols="12"
                >3.情緒狀況: 憂鬱量表: {{ filtermelancholy }} 分</v-col
              >
              <v-col cols="12">4.經濟來源: {{ list.economicsources }}</v-col>
              <v-col cols="12">5.家庭支持功能: {{ list.familysupport }}</v-col>
            </v-row>
          </td>
        </tr>

        <tr>
          <td class="font-weight-bold">三、個案處遇計畫</td>
        </tr>

        <tr>
          <v-row>
            <v-col cols="12">【服務對象問題及需求】 {{ list.qa }}</v-col>
            <v-col cols="12">【服務目標】 {{ list.target }} </v-col>
            <v-col cols="12"
              >【服務策略】 <br />
              ➤執行方法: {{ list.means }} <br />
              ➤服務資源： {{ list.resources }}<br />
              ➤執行期程： {{ list.schedule }}<br />
              ➤執行者： {{ list.executor }}
            </v-col>
            <v-col cols="12">【評值】 {{ list.evaluation }}</v-col>
          </v-row>
        </tr>
      </table>

      <v-divider></v-divider>

      <v-card-actions></v-card-actions>
      <!-- <pre>{{list}}</pre> -->
    </div>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  data() {
    return {
      cData: {},
      pData: {},
      uData: {},
      list: {}, //表單內資料
      adaptabilityItems: [],
      melancholyItems: [],
      NursingassessmentItems: [],
      pic_url: "",
    };
  },

  filters: {
    filterName(val) {
      if (val != undefined) {
        var name = val.split("(");
        return name[0];
      }
    },
    filterq(val) {
      if (val != undefined) {
        if (val == "1") {
          return "√";
        } else {
          return "";
        }
      }
    },
  },

  computed: {
    age() {
      return dayjs().format("YYYY") - dayjs(this.uData.birthday).format("YYYY");
    },
    //回傳適應性分數
    filteradaptability() {
      if (this.adaptabilityItems.length > 0) {
        var matchData = this.adaptabilityItems.filter((i) => {
          return i.date == this.list.date;
        });
        if (matchData.length > 0) {
          var data = JSON.parse(matchData[0].datalist);
          return data.total;
        } else {
          return "";
        }
      } else {
        return "";
      }
    },
    //回傳憂鬱分數
    filtermelancholy() {
      if (this.melancholyItems.length > 0) {
        var matchData = this.melancholyItems.filter((i) => {
          return i.date == this.list.date;
        });
        if (matchData.length > 0) {
          var data = JSON.parse(matchData[0].datalist);
          return data.total;
        } else {
          return "";
        }
      } else {
        return "";
      }
    },
    //回傳教育程度
    filtereducation() {
      if (this.NursingassessmentItems.length > 0) {
        var item = this.NursingassessmentItems[0];

        var rs = "";
        if (item.education1 == "true") {
          rs += "不識字;";
        }
        if (item.education2 == "true") {
          rs += "國小以下;";
        }
        if (item.education3 == "true") {
          rs += "小學";
        }
        if (item.education4 == "true") {
          rs += "國中";
        }
        if (item.education5 == "true") {
          rs += "高中(職)";
        }
        if (item.education6 == "true") {
          rs += "大學/大專;";
        }
        if (item.education7 == "true") {
          rs += "大學以上;";
        }
        if (item.education8 == "true") {
          rs += "不確定;";
        }

        return rs;
      }
    },
    //回傳身分別
    filteridentity() {
      if (this.NursingassessmentItems.length > 0) {
        var item = this.NursingassessmentItems[0];

        var rs = "";
        if (item.identity1 == "true") {
          rs += "一般;";
        }
        if (item.identity2 == "true") {
          rs += "中低收入戶;";
        }
        if (item.identity3 == "true") {
          rs += "低收入(一款)";
        }
        if (item.identity4 == "true") {
          rs += "低收入(二款)";
        }
        if (item.identity5 == "true") {
          rs += "低收入(三款)";
        }
        if (item.identity6 == "true") {
          rs += "原住民;";
        }
        if (item.identity7 == "true") {
          rs += "榮民(榮眷);";
        }
        if (item.identity8 == "true") {
          rs += "其他:";
        }
        if (item.identity9 && item.identity9 != "") {
          rs += item.identity9 + ";";
        }
        return rs;
      }
    },
  },

  mounted() {
    this.cData = JSON.parse(sessionStorage.getItem("cData"));
    this.pData = JSON.parse(sessionStorage.getItem("pData"));
    this.uData = JSON.parse(sessionStorage.getItem("uData"));

    this.list = JSON.parse(sessionStorage.getItem("printData"));
    this.getAdaptability(); //取得適應性評估表
    this.getMelancholy(); //取得憂鬱量表
    this.getNursingassessment(); //取得入院護理評估
  },

  methods: {
    getAdaptability() {
      var postData = {
        key: "u_snkey",
        value: this.uData.snkey,
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/adaptability",
          qs.stringify(postData)
        )
        .then((rs) => {
          this.adaptabilityItems = rs.data;
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    getMelancholy() {
      var postData = {
        key: "u_snkey",
        value: this.uData.snkey,
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" + this.$store.state.databaseName + "/melancholy",
          qs.stringify(postData)
        )
        .then((rs) => {
          this.melancholyItems = rs.data;
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    getNursingassessment() {
      var postData = {
        key: "u_snkey",
        value: this.uData.snkey,
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/nursingassessment",
          qs.stringify(postData)
        )
        .then((rs) => {
          this.NursingassessmentItems = rs.data;
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.TreatmentPrint {
  @import "node_modules/bootstrap/scss/bootstrap";
  @import "node_modules/bootstrap-vue/src/index.scss";

  .form_title {
    font-size: 1.2rem;
    // text-align: center;
    font-family: "微軟正黑體";
  }

  td {
    border: 1px #000000 solid;
  }
}
</style>